.container {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 3%;
}

.content {
    width: 60vw;
    margin: 4% 2%;
}

@media only screen and (max-width: 770px) {
    .container {
        margin: 7% 10%;
    }
    .content {
        width: 90vw;
        margin: 8vh 0;
    }
}